.opacity {
  right: -100%;
  background: #000;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  min-width: 100%;
  min-height: 100%;
  transition: all ease 0.5s;
}
.opacity_active {
  opacity: 0.7;
}

.relative {
  position: relative;
}

.swiper {
  overflow: hidden;
}

.swiper-button-prev,
.swiper-button-next {
  width: 45px !important;
  height: 45px !important;
  background: white;
  border-radius: 999px;
  border: 1px solid #353535;
}

.swiper-button-prev {
  transform: translateX(-50%);
  position: absolute !important;
  left: 20px !important;
}
.swiper-button-prev::after {
  content: "";
  display: none;
}

.swiper-button-next {
  transform: translateX(50%);
  position: absolute;
  right: 20px !important;
}
.swiper-button-next::after {
  content: "";
  display: none;
}

.header {
  padding: 14px 0;
  background: #fff;
  border-bottom: 1px solid #000;
}
.header__wrapper {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  max-width: 1140px;
  padding: 0 10px;
  margin: 0 auto;
}
.header__right {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  max-width: 950px;
  width: 100%;
  transition: all ease 0.5s;
}
.header__menu {
  display: flex;
  gap: 24px;
}
.header__link {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #000;
  padding-bottom: 3px;
  position: relative;
}
.header__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #000;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}
.header__link:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}
.header__info {
  display: flex;
  align-items: center;
  gap: 60px;
}
.header__lang {
  display: flex;
  align-items: center;
  gap: 4px;
}
.header .current {
  opacity: 0.5;
}
.header .current::after {
  content: unset;
}
.header__stick {
  padding-bottom: 5px;
}
.header__button {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #D61322;
  background: #fff;
  padding: 16px 36px;
  border: 1px solid #D61322;
  transition: all ease 0.5s;
}
.header__button:hover {
  background: #D61322;
  color: #fff;
}
.header .burger-btn {
  display: none;
  z-index: 10;
  position: relative;
  width: 30px;
  height: 20px;
  cursor: pointer;
}
.header .burger-btn > span {
  position: absolute;
  background-color: #000;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  top: 9px;
  transition: all ease 0.5s;
}
.header .burger-btn:after, .header .burger-btn:before {
  content: "";
  position: absolute;
  background-color: #000;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  transition: all ease 0.5s;
}
.header .burger-btn:before {
  top: 20px;
}
.header .burger-btn:after {
  bottom: 20px;
}
.header .burger-btn_active span {
  opacity: 0;
  background-color: #D61322;
}
.header .burger-btn_active:before {
  transform: rotate(45deg);
  top: 9px;
  background-color: #D61322;
}
.header .burger-btn_active:after {
  transform: rotate(-45deg);
  bottom: 9px;
  background-color: #D61322;
}
@media screen and (max-width: 920px) {
  .header .burger-btn {
    display: block;
  }
  .header__right {
    max-width: 320px;
    width: 100%;
    flex-direction: column;
    background: #000;
    padding: 100px 20px 50px 20px;
    position: absolute;
    transform-origin: right;
    transform: scaleX(0);
    top: 0;
    right: -100%;
    gap: 30px;
  }
  .header__right_active {
    transform: scaleX(1);
    right: 0;
  }
  .header__menu {
    flex-direction: column;
    width: 100%;
  }
  .header__link {
    color: #fff;
  }
  .header__link::after {
    background-color: #fff;
  }
  .header__info {
    gap: unset;
    justify-content: space-between;
    width: 100%;
  }
  .header__stick {
    color: #fff;
  }
  .header__button {
    background: #D61322;
    color: #fff;
  }
}

